import { useState } from 'react'
import { Box } from '@mui/material'
import HoverContainer from '../Containers/HoverContainer'
import Flex from '../Containers/Flex'
import Grid from '../Containers/Grid'
import InputBlock from '../Inputs/InputBlock'
import DynamicButton from '../Buttons/DynamicButton'
import uniqid from 'uniqid'

function Position() {
  const [position, setPosition] = useState('')
  const [onEdit, setOnEdit] = useState(false)

  function handleOnChange(e) {
    setPosition(e.target.value)
  }

  function handleDone() {
    setOnEdit(false)
  }

  return (
    <HoverContainer fn={setOnEdit} onEdit={onEdit}>
      <Grid>
        {onEdit ? (
          <PositionEdit
            position={position}
            onChange={handleOnChange}
            onDone={handleDone}
          />
        ) : (
          <Box height='100%'>{position || 'Position'}</Box>
        )}
      </Grid>
    </HoverContainer>
  )
}

function PositionEdit({ position, onChange, onDone }) {
  const positions = [
    'App Developer',
    'UX Designer',
    'QA Tester',
    'Tech Writer',
    'Data Analyst',
    'Project Manager',
    'Marketing Specialist',
    'Content Creator',
    'Business Analyst',
    'Support Specialist',
  ]

  function getRandomPosition(array) {
    return array[Math.floor(Math.random() * array.length)]
  }

  return (
    <Grid>
      <InputBlock
        htmlFor={uniqid.toString()}
        name='Position'
        value={position}
        color='primary.opposite'
        bgcolor='primary.main'
        placeholder={`e.g ${getRandomPosition(positions)}`}
        onChange={onChange}
      />
      <Flex type='end'>
        <DynamicButton
          text='Done'
          type='button done contained medium'
          mainColor='black'
          onClick={onDone}
        />
      </Flex>
    </Grid>
  )
}

export default Position
